import React from "react";
import { Switch, Route } from "react-router-dom";

import "./App.css";

import HomePage from "./pages/homePage/HomePage.component";
import ShopPage from "./pages/shop/shop.component";
function App() {
  return (
    <div>
      <div>Online</div>
      <Switch>
        <Route exact path="/e-commerce-react-app/" component={HomePage} />
        <Route exact path="/e-commerce-react-app/shop" component={ShopPage} />
      </Switch>
    </div>
  );
}

export default App;
